<template>
    <el-table :data="envirmentData" class="table" stripe height="85%" border>
        <el-table-column prop="RecordedAt" label="日期" min-width="180">
            <template slot-scope="scope">
                {{ formatDate(scope.row.RecordedAt) }}
            </template>
        </el-table-column>
        <el-table-column prop="LightIntensity" label="光照">
        </el-table-column>
        <el-table-column prop="Humidity" label="湿度">
        </el-table-column>
        <el-table-column prop="Temperature" label="温度">
        </el-table-column>
    </el-table>
</template>

<script>
export default {
    props: ["envirmentData"],
    methods: {
        formatDate(dateString) {
            const date = new Date(dateString);
            const year = date.getFullYear();
            const month = String(date.getMonth() + 1).padStart(2, '0'); // 月份是从0开始的
            const day = String(date.getDate()).padStart(2, '0');
            const hours = String(date.getHours()).padStart(2, '0');
            const minutes = String(date.getMinutes()).padStart(2, '0');
            const seconds = String(date.getSeconds()).padStart(2, '0');

            return `${year}年${month}月${day}日 ${hours}:${minutes}:${seconds}`;
        }
    }
}
</script>
<style scoped>
.table {
    width: 90%;
    height: 80%;
    margin: 10% 20px 0;
    overflow: scroll;
    font-weight: 900;
}

.table::-webkit-scrollbar {
    display: none;
}
</style>