<template>
  <div class="button" style="width: 100%; height: 100%">
    <el-button :type="!Temperature ? 'info' : 'success'" round  @click="toggleState('Temperature')" style="grid-area: Line1;">
      湿度: {{ Temperature ? "开" : "关" }}
    </el-button>
    <el-button :type="!Light ? 'info' : 'success'" round  @click="toggleState('Light')" style="grid-area: Line2;">
      光照: {{ Light ? "开" : "关" }}
    </el-button>
    <el-button :type="!AirConditioning ? 'info' : 'success'" round  @click="toggleState('AirConditioning')" style="grid-area: Line3;">
      空调: {{ AirConditioning ? "开" : "关" }}
    </el-button>
    <el-button type="warning" round @click="logout" plain style="grid-area: Line4;">登出</el-button>
  </div>
</template>

<script>
export default {
  props: ["ws"],
  data() {
    return {
      Temperature: false,
      Light: false,
      AirConditioning: false
    };
  },
  methods: {
    toggleState(device) {
      this[device] = !this[device];
      const state = this[device] ? "1" : "0";
      const index = device === 'Temperature' ? '0' :
                    device === 'Light' ? '1' : '2';
      this.ws.send(index + state);
    },
    logout(){
      this.$cookies.remove('frost_cookie');
      window.location.href = '/';
    }
    
  }
};
</script>

<style scoped>
*{
  margin: 0 !important;
  padding: 0 !important;
}

.button{
  display: grid;
  grid-template-rows: 3fr 1fr 1fr 3fr;
  grid-template-columns:1fr 2fr 2fr 1fr;
  grid-template-areas:
    "LineA LineA LineA LineA"
    "LineC Line1 Line2 LineB"
    "LineC Line3 Line4 LineB"
    "LineD LineD LineD LineD";
    row-gap: 20px;
    column-gap: 20px;
}
</style>
