<template>
  <div class="box">
    <div class="title">
      智慧大棚监测管理系统
    </div>
    <TableEnvironmentData :envirmentData="EnvirmentData" style="grid-area: table;" />
    <ControlCompoment :ws="socket" style="grid-area: Line1;"/>
    <HumidityEcharts :Humidity="Humidity" :date="date" ref="Humidity" class="charts" style="grid-area: Line2;" />
    <LightIntensityEcharts :LightIntensity="LightIntensity" :date="date" ref="LightIntensity" class="charts"
      style="grid-area: Line3;" />
    <TemperatureEcharts :Temperature="Temperature" :date="date" ref="Temperature" class="charts"
      style="grid-area: Line4;" />
  </div>
</template>

<script>
import axios from 'axios'
import HumidityEcharts from './HumidityEcharts.vue'
import LightIntensityEcharts from './LightIntensityEcharts.vue'
import TemperatureEcharts from './TemperatureEcharts.vue'
import TableEnvironmentData from './TableEnvirmentData.vue'
import ControlCompoment from './ControlCompoment.vue'

export default {
  components: {
    HumidityEcharts,
    LightIntensityEcharts,
    TemperatureEcharts,
    TableEnvironmentData,
    ControlCompoment
  },
  data() {
    return {
      EnvirmentData: [],
      date: [],
      Humidity: [],
      LightIntensity: [],
      Temperature: [],
      socket: ""
    };
  },
  mounted() {
    this.getEnvirmentData()
  },
  methods: {
    getEnvirmentData() {
      axios.get("http://localhost:3000/api/parameters")
        .then(res => {
          this.EnvirmentData = res.data.data.reverse();
          this.EnvirmentData.forEach(element => {
            this.LightIntensity.unshift(element.LightIntensity);
            this.Humidity.unshift(element.Humidity);
            this.Temperature.unshift(element.Temperature);
            this.date.unshift(this.formatDate(element.RecordedAt));
          });

        })
        .then(() => {
          this.$refs.Humidity.HumidityEcharts()
          this.$refs.LightIntensity.LightIntensityEcharts()
          this.$refs.Temperature.TemperatureEcharts()
        })
        .then(() => {
          this.socket = new WebSocket('ws://127.0.0.1:3000/ws');
          this.socket.onopen = function () {
            console.log('WebSocket connection established');
          };
          this.socket.onmessage = (event) => {
            var data = JSON.parse(event.data);
            this.EnvirmentData.unshift(data);
            this.LightIntensity.push(data.LightIntensity);
            this.Humidity.push(data.Humidity);
            this.Temperature.push(data.Temperature);
            let message = ""
            if(data.Humidity<30||data.Humidity>80){
                message+= '湿度异常'
            }
            if(data.LightIntensity<100||data.LightIntensity>300){
                message+=',光照强度异常'
            }
            if(data.Temperature<15||data.Temperature>30){
                message+= ',温度异常'
            }
            if(message!=""){
              this.$message.error(message)
            }
            this.date.push(this.formatDate(data.RecordedAt))
            this.$refs.Humidity.HumidityEcharts()
            this.$refs.LightIntensity.LightIntensityEcharts()
            this.$refs.Temperature.TemperatureEcharts()
          };
          this.socket.onclose = function () {
            console.log('WebSocket connection closed');
          };
        })
    },
    handleClick(tab, event) {
      console.log(tab, event);
    },
    formatDate(dateString) {
      const date = new Date(dateString);
      const year = date.getFullYear();
      const month = String(date.getMonth() + 1).padStart(2, '0'); // 月份是从0开始的
      const day = String(date.getDate()).padStart(2, '0');
      const hours = String(date.getHours()).padStart(2, '0');
      const minutes = String(date.getMinutes()).padStart(2, '0');
      const seconds = String(date.getSeconds()).padStart(2, '0');

      return `${year}年${month}月${day}日 ${hours}:${minutes}:${seconds}`;
    }
  }
}
</script>

<style scoped>
* {
  padding: 0;
  border: 0;
  margin: 0;
}

.box {
  display: grid;
  height: calc(100vh - 16px);
  margin: 0 30px;
  padding: 0 20px;
  background-color: rgba(255, 255, 255, .6);
  overflow: hidden;
  grid-template-rows: 60px 1fr 1fr;
  grid-template-columns: 3fr 2fr 2fr;
  grid-template-areas:
    "header1 header1 header2"
    "table  Line1 Line2"
    "table Line3 Line4";
  grid-column-gap: 20px;
  grid-row-gap: 20px;
}

.charts {
  width: 100% !important;
}

.title {
  margin-left: 120px;
  line-height: 92px;
  font-size: 32px;
  font-weight: 900;
  grid-area: header1;
}
</style>