<template>
  <div id="app">
    <MainTabs></MainTabs>
  </div>
</template>

<script>
import MainTabs from "./components/MainTabs.vue";
import axios from "axios";

export default {
  name: "App",
  components: {
    MainTabs,
  },
  mounted() {
    axios.get('/api/checkusers').then((res) => {
      if (!res.data.hasuser) {
        window.location.href = "http://localhost:3000"
      }
    }).catch((err) => {
      window.location.href = "http://localhost:3000"
    })
  },
};
</script>

<style>
body {
  background: linear-gradient(to bottom right, #d1b3ff, #b3d9ff) !important;
}
</style>