<template>
    <div id="LightIntensity" class="LightIntensity" style="width: 100%; height: 100%"></div>
</template>

<script>


export default {
    props: ["LightIntensity", "date"],
    methods: {
        LightIntensityEcharts() {
            var chartDom = document.getElementById('LightIntensity');
            var myChart = this.$echarts.init(chartDom);
            var option;

            option = {
                title: {
                    text: 'LightIntensity'
                },
                tooltip: {
                    trigger: 'axis'
                },
                legend: {},
                textStyle: {
                    color: 'black',
                    fontWeight: 700
                },
                toolbox: {
                    show: true,
                    feature: {
                        dataZoom: {
                            yAxisIndex: 'none'
                        },
                        dataView: { readOnly: false },
                        saveAsImage: {}
                    }
                },
                xAxis: {
                    type: 'category',
                    boundaryGap: false,
                    data: this.date
                },
                yAxis: {
                    type: 'value',
                    axisLabel: {
                        formatter: '{value} '
                    }
                },
                series: [
                    {
                        name: '光照强度',
                        type: 'line',
                        data: this.LightIntensity,
                        markPoint: {
                            data: [
                                { type: 'max', name: 'Max' },
                                { type: 'min', name: 'Min' }
                            ]
                        },
                        markLine: {
                            data: [{ type: 'average', name: 'Avg' }]
                        }
                    }
                ],
                color: ['#F56C6C'],
                dataZoom: [
                    {
                        type: 'slider',
                        show: true,
                        xAxisIndex: [0],
                        startValue: this.date.length > 15 ? this.date.length - 15 : 0,
                        endValue: this.date.length - 1
                    },
                    {
                        type: 'inside',
                        xAxisIndex: [0],
                        start: 0,
                        end: 100
                    }
                ]
            };

            option && myChart.setOption(option);
        },
    }
};
</script>